import React from "react";
import { useParams } from "react-router-dom";

import { CCard, CCardHeader, CCardBody } from "@coreui/react";
import { useSelector } from "react-redux";

function findSubjectById(array, id) {
  var subject;
  for (let index = 0; index < array.length; index++) {
    for (let i = 0; i < array[index]["days"].length; i++) {
      array[index]["days"][i].subjects.find(function (element) {
        if (element.id === Number(id)) {
          subject = element;
          return element;
        }
      });
    }
  }
  return subject;
}

const findSurveyByTitle = (array, title) => {
  const result = array.filter((survey) => survey.surveyTitle === title);
  return result[0];
};

const Survey = () => {
  /**
   * Todo: Fetch subject by id
   * @info when this component mounting fetch specific subject by its id
   */
  const Surveys = useSelector((state) => state.surveys);
  let { title } = useParams();
  let survey = findSurveyByTitle(Surveys, title);

  // let Objectives = subject["learningObjectives"].split(",");

  return (
    <>
      <CCard>
        <CCardHeader style={{ margin: "0 auto" }} className="h1">
          {survey.surveyTitle}
        </CCardHeader>
        <CCardBody>
          <div style={{ paddingLeft: "2em" }}>
            <p style={{ paddingTop: "1em" }}>{survey.surveyDescreption}</p>
            <p>URL: {survey.surveyURL}</p>
          </div>
        </CCardBody>
      </CCard>
    </>
  );
};
export default Survey;
